<template>
  <div>
    <generic-grid
      :gridName="gridName"
      :isHeaderConfigurable="true"
      :creatable="false"
      :service="service"
      :params="params"
      :headers="gridHeaders"
      :toolbarTitle="toolbarTitle"
    >
      <template v-slot:routeLinkName="{ item }">
        <text-button :text="item.name" :onClick="() => redirectTo(dialogNames.viewStreamClones, item)"></text-button>
      </template>
    </generic-grid>
  </div>
</template>
<script>
import ROUTES from '../../../constants/routes';
import TextButton from '../../Common/TextButton.vue';
import IconButton from '../../Common/IconButton.vue';
import GenericGrid from '../../Common/GenericGrid.vue';
import * as gridNames from '../../../constants/gridNames';
import { rfFlowNetworkCloneService } from '../../../services/rfFlowNetworkCloneService';

const dialogNames = {
  viewStreamClones: 'viewStreamClones',
};

const redirectMap = {
  [dialogNames.viewStreamClones]: (flowNetworkId, streamId) =>
    ROUTES.rfFlowNetworkStreamsClone.replace(':flowNetworkId', flowNetworkId).replace(':streamId', streamId),
};

export default {
  components: { GenericGrid, IconButton, TextButton },
  name: 'flowNetworkGrid',
  data() {
    return {
      params: {
        with_streams: true,
      },
      gridName: gridNames.RF_FLOW_NETWORK_CLONES,
      service: rfFlowNetworkCloneService,
      refreshGridCb: f => f,
      toolbarTitle: 'Flow Network Clones',
      selectedItem: {},
      dialogNames: { ...dialogNames },
      dialog: {
        [dialogNames.associateStreams]: false,
      },
      selectedItem: {},
      gridHeaders: [
        {
          text: 'Name',
          align: 'center',
          value: 'name',
          sortable: true,
        },
        {
          text: 'Remote Flow Id',
          align: 'center',
          value: 'remote_flow_uuid',
          sortable: true,
        },
        {
          text: 'Global Flow Id',
          align: 'center',
          value: 'global_flow_id',
          sortable: true,
        },
        {
          text: 'Actions',
          align: 'center',
          value: 'actions',
          sortable: true,
        },
      ],
    };
  },
  computed: {
    flowNetworkId() {
      return this.$route.params.flowNetworkId;
    },
    streamId() {
      return this.$route.params.streamId;
    },
  },
  methods: {
    redirectTo(viewName, item) {
      const streamId = item.uuid;
      if (!streamId) {
        return;
      }
      let destination = redirectMap[viewName];
      if (typeof destination === 'function') {
        this.$router.push({ path: destination(streamId) });
      }
    },
    toggleDialog(dialogName, item, refreshGrid) {
      this.selectedItem = item;
      this.dialog[dialogName] = !this.dialog[dialogName];
      this.refreshGridCb = refreshGrid;
      if (this.dialog[dialogName]) {
        this.selectedItem = item;
      } else {
        this.selectedItem = null;
      }
    },
    closeDialog(dialogName) {
      this.dialog[dialogName] = false;
      this.selectedItem = {};
    },
  },
};
</script>
